import { useRef } from 'react';
import Detail from './Detail';
import useOnCLickOutside from '../../helpers/clickOutside';

export default function EditDetails({ details, handleChange, updateDetails, infos, setVisible }) {
	const modal = useRef(null);
	useOnCLickOutside(modal, () => setVisible(false));
	return (
		<div className="blur">
			<div className="postBox infosBox" ref={modal}>
				<div className="box_header">
					<div className="small_circle" onClick={() => setVisible(false)}>
						<i className="exit_icon"></i>
					</div>
					<span>Chnh sa chi tit</span>
				</div>
				<div className="details_wrapper scrollbar">
					<div className="details_col">
						<span>Chnh sa gii thiu bn thn</span>
						<span>Chi tit thng tin bn mun cng khai</span>
					</div>
					<div className="details_header">Tn khc</div>
					<Detail
						value={details?.otherName}
						img="studies"
						placeholder="Thm tn khc"
						name="otherName"
						text="tn khc"
						handleChange={handleChange}
						updateDetails={updateDetails}
						infos={infos}
					/>
					<div className="details_header">Cng vic</div>
					<Detail
						value={details?.job}
						img="job"
						placeholder="Thm cng vic"
						name="job"
						text="cng vic"
						handleChange={handleChange}
						updateDetails={updateDetails}
						infos={infos}
					/>
					<Detail
						value={details?.workplace}
						img="job"
						placeholder="Thm ni lm vic"
						name="workplace"
						text="ni lm vic"
						handleChange={handleChange}
						updateDetails={updateDetails}
						infos={infos}
					/>
					<div className="details_header">Gio dc</div>
					<Detail
						value={details?.highSchool}
						img="studies"
						placeholder="Thm trng THPT"
						name="highSchool"
						text="trng THPT"
						handleChange={handleChange}
						updateDetails={updateDetails}
						infos={infos}
					/>
					<Detail
						value={details?.college}
						img="studies"
						placeholder="Thm trng i hc/Cao ng"
						name="college"
						text="trng i hc/ Cao ng"
						handleChange={handleChange}
						updateDetails={updateDetails}
						infos={infos}
					/>
					<div className="details_header">Thnh ph hin nay</div>
					<Detail
						value={details?.currentCity}
						img="home"
						placeholder="Thm thnh ph hin nay"
						name="currentCity"
						text="thnh ph hin nay"
						handleChange={handleChange}
						updateDetails={updateDetails}
						infos={infos}
					/>
					<div className="details_header">Qu qun</div>
					<Detail
						value={details?.hometown}
						img="home"
						placeholder="Thm qu qun"
						name="hometown"
						text="qu qun"
						handleChange={handleChange}
						updateDetails={updateDetails}
						infos={infos}
					/>
					<div className="details_header">Tnh trng hn nhn</div>
					<Detail
						value={details?.relationship}
						img="relationship"
						placeholder="Thm tnh trng hn nhn"
						name="relationship"
						text="tnh trng hn nhn"
						handleChange={handleChange}
						updateDetails={updateDetails}
						infos={infos}
						rel
					/>
					<div className="details_header">Zalo</div>
					<Detail
						value={details?.zalo}
						img="home"
						placeholder="Thm S in thoi Zalo: vd: 0987xxxxxx"
						name="zalo"
						text="zalo"
						handleChange={handleChange}
						updateDetails={updateDetails}
						infos={infos}
					/>
				</div>
			</div>
		</div>
	);
}
