export default function Bio({
	infos,
	handleChange,
	max,
	setShowBio,
	updateDetails,
	placeholder,
	name,
	detail,
	setShow,
	rel,
}) {
	return (
		<div className="add_bio_wrap">
			{rel ? (
				<select className="select_rel" name={name} value={infos.relationship} onChange={handleChange}>
					<option value="c thn">c thn</option>
					<option value="ang hn h">ang hn h</option>
					<option value=" kt hn"> kt hn</option>
					<option value=" ly hn"> ly hn</option>
					<option value="Quan h phc tp">Quan h phc tp</option>
				</select>
			) : (
				<textarea
					placeholder={placeholder}
					name={name}
					value={infos?.[name]}
					maxLength={detail ? 25 : 100}
					className="textarea_blue details_input"
					onChange={handleChange}
				></textarea>
			)}
			{!detail && <div className="remaining">{max} k t cn li</div>}
			<div className="flex">
				<div className="flex flex_right">
					<button className="gray_btn" onClick={() => (!detail ? setShowBio(false) : setShow(false))}>
						Hy
					</button>
					<button
						className="blue_btn"
						onClick={() => {
							updateDetails();
							setShow(false);
						}}
					>
						Lu
					</button>
				</div>
			</div>
		</div>
	);
}
